@import url(https://fonts.googleapis.com/css?family=Lato:300,400,500,700&display=swap);
@import url(https://fonts.googleapis.com/css?family=Raleway:300,400,500,700&display=swap);
@import url(https://fonts.googleapis.com/css?family=Lato:300,400,500,700&display=swap);
@import url(https://fonts.googleapis.com/css?family=Lato:300,400,500,700&display=swap);
@import url(https://fonts.googleapis.com/css?family=Lato:300,400,500,700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.App {
  font-family: 'Lato';
  height: 100%;
}

.launcherImg {
    max-height: 28vh;
    width: auto;
}

.notesContainer {
    margin-top: 17vh;
}

.headingMain {
    color: #3E3E3E;
    text-align: center;
    font-family: 'Raleway';
    font-size: 7vh;
}
.NoteItem {
    padding: 2vw 2vw 2vw 7vw !important; 
}

.NoteItem:hover {
    cursor: pointer;
}

.noteTitle {
    font-weight: bolder;
    font-size: 5vw;
    font-family: 'Lato';
}

.noteContent {
    font-family: 'Lato';
    overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-line-clamp: 2;
    -webkit-box-orient: vertical;
}

.lastEdit {
    color: #424242;
    font-size: 85%;
    font-family: 'Lato';
}

.deleteIcon:hover {
    cursor: pointer;
}
.titleEditor {
    padding: 5vh;
    margin: 20vh 7vh 7vh 7vh;
    font-family: 'Lato';
}

.contentEditor {
    padding: 5vh;
    margin: 1vh 7vh 7vh 7vh;
    font-family: 'Lato';
}

.newNoteContainer {
    background-color: #0586ff;
    min-height: 100vh;
    bottom: 0;
}

.nav {
    padding-bottom: 5vh;
    background-color: #0586ff;
}
.input{
    width: 100%;
}
.successMessage{
    font-weight: bolder;
    font-size: 120%;
}

.contactHead {
    font-family: 'Raleway';
    text-align: center;
    padding: 5vh;
    font-size: 5vh;
    font-weight: bolder;
}

.bottomNav {
    padding: 10vh 0vh 7vh 0;
}

.credits {
    font-size: 80%;
    padding-bottom: 7vh;
}
